import React, {Component} from 'react';
import Modal from 'react-responsive-modal';
import './Button.css';
import ModalContent from "./ModalContent";
class Button extends Component{

    constructor(){
        super();
        this.state = {
            open: false
        };
        this.onCloseModal = this.onCloseModal.bind(this);
        this.onOpenModal = this.onOpenModal.bind(this);
    }

    onOpenModal(){
        this.setState({
            open: true
        });
    }

    onCloseModal(){
        this.setState({
            open: false
        });
    }

    render(){
        let {open} = this.state;
        return(
            <div className="column is-half">
                <button className={this.props.classNames} onClick={this.onOpenModal}>{this.props.name}</button>
                <Modal open={open} onClose={this.onCloseModal}>
                    <ModalContent name={this.props.name} onSubmit={this.onCloseModal} date={this.props.date}/>
                </Modal>
            </div>
        )
    }

}

export default Button;