import React from 'react'
import Button from "./Button";


const Service = ({day, date, time}) =>
    <div className="column is-one-quarter-desktop is-half-tablet is-narrow">
        <div className="notification is-white is-rounded box">
            <h1 className="title is-size-5 ">
                {day} <br/>
                {date} <br/>
                {time}
            </h1>
            <div className="columns">
                <Button classNames="button is-outlined is-danger" name="Decline" date={date}/>
                <Button classNames="button is-outlined is-success" name="Accept" date={date}/>
            </div>
        </div>
    </div>;

export default Service;