import React, {Component} from 'react';

let songs = [
    {
        id: randomId(),
        title: "Who you say I am"
    },
    {
        id: randomId(),
        title: "Peace"
    },
    {
        id: randomId(),
        title: "Wonder"
    },
    {
        id: randomId(),
        title: "Reckless Love"
    },
    {
        id: randomId(),
        title: "Love Won't Let Me Down"
    }
];
let randSong = songs[Math.floor(Math.random() * songs.length)].title;

class ModalContent extends Component{

    render(){
        let {name, date} = this.props;
        if(name === "Accept"){
            return(
                <div>
                    <p className="title has-text-centered">
                        <span role="img" aria-label="party">ðŸŽ‰</span>
                    </p>
                    <p className="title is-5">
                        <span className="has-text-warning is-uppercase">{date}</span><br/>
                        It's never the same if you're not there! Vote for 3 songs down here ðŸ‘‡
                    </p>
                    <div className="columns is-centered">
                        <div className="column is-8">
                            {Array.from(Array(3), (e, i) =>
                                <div className="field" key={i}>
                                    <div className="control">
                                        <div className="select is-primary is-rounded">
                                            <select name="select-one-song" id="">
                                                <option value=""></option>
                                                {songs.map( (entry, i) =>
                                                    <option key={i} value={entry.id}>{entry.title}</option>
                                                )}
                                            </select>
                                        </div>
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                    <div className="columns is-centered">
                        <div className="column is-3">
                            <button className="button is-success is-rounded is-outlined" onClick={this.props.onSubmit}>Submit</button>
                        </div>
                    </div>
                    <p>60% of your team members love <strong>{randSong}!</strong></p>
                </div>
            );
        }else if(name === "Decline"){
            return(
                <div>
                    <p className="title has-text-centered">
                        <span role="img" aria-label="crying emoji">ðŸ˜¢</span>
                        </p>
                    <p className="title is-5">
                        <span className="has-text-warning is-uppercase">{date}</span> <br/>
                        Is too sad that you're not coming, could you let us know why?
                    </p>
                    <div className="field">
                        <div className="control">
                            <textarea name="reason" id="reason" rows="7" className="textarea is-small is-rounded has-fixed-size"></textarea>
                        </div>
                    </div>
                    <div className="columns is-centered">
                        <div className="column is-3">
                            <button className="button is-danger is-rounded is-outlined" onClick={this.props.onSubmit}>Submit</button>
                        </div>
                    </div>
                </div>
            )
        }else {
            return(
                <p>Not Found</p>
            )
        }


    }
}

function randomId(){
    let res = "";
    while(res.length < 10){
        res += Math.floor(Math.random()*10);
    }
    return res;
}

export default ModalContent;